//         font-size: 1.2em;
//     }
// }
@media (max-width:970px) {
  .nome-applicativo {
    font-size: 1.2em;
  }
}

// stile per bottone esci
.exit-button-container {
  padding: 0;
}

[class*=" ico-"]:before {
  vertical-align: middle;
}

.exit-button {
  margin: 0;
  a {
      color: @orange;
      display: table;
      padding: 0em 1.250em 0em 1.250em;
      margin: .5em 0 .5em 0;
      text-decoration: none;
      cursor: pointer;
      width: 100%;
      height: 2.813em;
      width: 19.375em;
      &:hover,
      &:focus,
      &:active {
          background-color: @orange;
          color: @darkest_gray;
          }
      table {
          width: 100%;
          display: table-cell;
          vertical-align: middle;
      }
  }
}

.exit-image-container {
  // width: 1.750em;
  // height: 1.750em;
  div {
      vertical-align: middle;
      font-size: 1.563em !important;
      margin: 0 !important;
  }
  img {
      position: relative;
      border-radius: 50%;
      width: 100%;
  }
}
.exit-info {
  padding-left: 0.625em;
  vertical-align: middle;
  line-height: 1;
  font-weight: normal;
}


:host-context(.contrasto-on) {
  .header {
    background-color: @white;
    .navbar {
      background-color: @black;
      .container {
        background-color: @black;
      }
    }
    .navbar-tape {
      background-color: @black;
    }
  }
  .header-overlay {
    background: rgba(255, 255, 255, 1);
  }
  .navbar {
    .container {
      .navbar-toggle {
        background-color: transparent;
        &:hover {
          background: @black;
        }
        &:focus {
          background: @black;
        }
        &:active {
          background: @black;
        }
      }
      .navbar-toggle-operation {
        background-color: transparent;
        &:hover {
          background: @white;
          color: @black;
          .icomoon-Modifica_matita {
            color: @black;
          }
          span {
            color: @black;
          }
        }
        &:focus {
          background: @white;
          color: @black;
          .icomoon-Modifica_matita {
            color: @black;
          }
          span {
            color: @black;
          }
        }
        &:active {
          background: @white;
          color: @black;
          .icomoon-Modifica_matita {
            color: @black;
          }
          span {
            color: @black;
          }
        }
        &.navbar-toggle-open {
          background: @white !important;
          color: @black;
          .icomoon-Modifica_matita {
            color: @black;
          }
          span {
            color: @black;
          }
        }
      }
      .navbar-info-cliente {
        background-color: transparent;
        border-color: @white;
        color: @white;
      }
    }
  }
  .navbar-toggle {
    .icon-bar {
      background-color: @white !important;
    }
    .ico-navbar-menu,
    .ico-comuni-chiudi,
    .icomoon-Modifica_matita {
      color: @white;
    }
  }
  .navbar-toggle-open {
    background: @black !important;
  }
  .prev-page-btn-container {
    .prev-page-btn {
      color: @black;
    }
  }
  .page-title-container {
    border-color: @white;
  }
  .nome-applicativo {
    color: @white;
  }
  .header-progress-container,
  .header-paging-container {
    border-color: @white;
  }
  .triangolo {
    border-color: @white;
    background:@black;
  }
  .container-ident-cliente {
    color: inherit;
  }
  .voce-ident-cliente {
    color: inherit;
  }
  .testo-ident-cliente {
    color: inherit;
  }

  .exit-button {
    a {
        color: @white;
        &:hover,
        &:focus,
        &:active {
            background-color: @white;
            color: @black;
            }
    }
  }
}
